import { Injector } from '@angular/core';
import { RxViewActionExpressionConfigurator } from '../expression-configurator/view-action-expression-configurator.class';
import { IExpressionConfigurator } from '@helix/platform/shared/api';
import {
  IViewActionDesignModel,
  IViewActionDesignProperties,
  IViewActionDesignSandbox
} from '@helix/platform/view/api';
import { IViewComponentDesignSetPayload } from '../public-interfaces';

export abstract class RxViewDesignerActionModel implements IViewActionDesignModel {
  readonly guid = this.sandbox.guid;

  protected expressionConfigurator = new RxViewActionExpressionConfigurator(
    this.injector,
    this.sandbox.descriptor.name,
    this.sandbox.guid
  );

  constructor(protected injector: Injector, readonly sandbox: IViewActionDesignSandbox) {}

  getExpressionConfigurator(): IExpressionConfigurator {
    return this.expressionConfigurator;
  }

  getPropertiesByName(): IViewActionDesignProperties {
    return this.sandbox.getActionProperties();
  }

  getChildren(): IViewComponentDesignSetPayload[] {
    return this.sandbox.getChildren();
  }

  getOutputExpressionForPropertyPath(propertyPath: string): string {
    return `\${view.components.${this.guid}.output.${propertyPath}}`;
  }
}
